@use './variaveis';

.shows{
    padding: 2vw 5.6vw;
    min-height: 50vw;

    &__list{
        margin-top: 44px;
        display: none;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 24px;

        @media (max-width: 768px) {
            grid-template-columns: 1fr 1fr;
        }

        &--is-active{
            display: grid;
        }

        &__item{
            list-style: none;

            img{
                max-width: 100%;
                border-radius: 4px;
            }
        }

    }

    &__tabs{
        display: flex;
        justify-content: center;

        @media (max-width: 768px){
            // Força os textos ficarem em 1 linha mesmo
            // que ultrapasse o container
            white-space: nowrap;
            overflow-x: scroll;
            padding-left: 20px;
        }

        &__button{
            color: variaveis.$corTextoSecundario;
            font-size: 20px;
            border: none;
            border-bottom: 5px solid transparent;
            padding-bottom: 5px;
            background: none;
            text-transform: uppercase;
            margin: 0 1vw;
            cursor: pointer;

            &--is-active{
                border-color: variaveis.$corTextoSecundario;
            }

            @media (max-width: 768px){
                font-size: 16px;
            }
        }
    }
}